@font-face {
    font-family: 'Fortnite';
    src: url('./fonts/fortnite.otf') format('opentype');
}

body {
    font-family: 'Fortnite', sans-serif;
}

.App {
  text-align: center;
}

.App-preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    opacity: 1;
    transition: opacity 1s ease-out;
}

.App-logo {
    width: 200px;
    height: auto;
    opacity: 0;
    animation: App-fadeIn 1s ease-in forwards;
}

@keyframes App-fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: fadeOut 1s ease-out 1s forwards;
}

.logo {
    font-size: 2em;
    opacity: 0;
    animation: fadeIn 1s ease-in forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    to {
        opacity: 0;
        visibility: hidden;
    }
}

* {
    box-sizing: border-box;
}

.game-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(to right, rgba(232, 44, 64, 1) 50%, rgba(137, 223, 111, 1) 50%);
    color: white;
    opacity: 0;
    transition: opacity 1s ease-in;
    overflow: hidden;
}

.character {
    margin: 0;
    height: 100%;
    font-size: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
    gap: 15px;
}

.play-button {
    margin: 0;
    border: 0;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2.5rem;
    font-family: 'Fortnite', sans-serif;
    font-size: 2em;
    background-color: transparent;
    padding: 8vh 35px 3vh 35px;
    text-align: center;
    text-shadow: 
            0px 1px 0px #640D0D,
            0px 2px 0px #640D0D,
            0px 3px 0px #640D0D,
            0px 4px 0px #640D0D;
    -webkit-text-stroke: 1px white;
    z-index: 101;
}

.play-button::before {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
  width: 100%;
  height: 95%;
  background-color: #ffffff;
  z-index: -1;
  transform: skewY(-5deg);
}

.vs-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30vh;
    z-index: 102;
}

.App-vs {
    width: 150px;
    height: auto;
}

.character-name {
  text-shadow: 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.App-left-character {
    width: 70vh;
    height: auto;
}

.App-right-character {
    width: 70vh;
    height: auto;
}